/* Leaderboard.css */
.leaderboard-container {
  margin: 20px 0;
  padding: 10px;
  background-color: #545151;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.leaderboard {
  width: 100%;
  border-collapse: collapse;
}

.leaderboard th,
.leaderboard td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
  color: #fff; /* Ensure text color is white */
}

.leaderboard th {
  background-color: #333;
  color: white;
}

.leaderboard tr {
  color: #fff; /* Ensure text color is white for all rows */
}

.highlight-current-user {
  font-weight: bold;
  background-color: green !important;
  color: #fff; /* Ensure highlighted text is white */
}

.highlight-reset {
  background-color: red;
  color: white; /* Ensure reset text is white */
}
